import { Link } from "react-router-dom";
import React from 'react'
import {styled} from "styled-components"
import {Box} from "@chakra-ui/react"
const Navbar = () => {
  return (
    <NAVWRAPPER id="nav-menu">
       <h1>Deepshikha</h1>
      <Link to={"/"} className="nav-link home">HOME</Link>
      <Link to={"/about"} className="nav-link about">ABOUT</Link>
      <Link to={"/project"} className="nav-link projects" >PROJECT</Link>
      <Link to={"/github"} className="nav-link git">GITHUB</Link>
      <Link to={"/skills"} className="nav-link skills">SKILLS</Link>
      <Link to={"/contact"} className="nav-link contact">CONTACT</Link>
      <Box bgColor="red.400" p={3} pt={1} className="nav-link resume" >
                <a id="resume-link-2" href="https://drive.google.com/file/d/1eYvu_wga1fN32_WcDARJB2etLEWhg0JZ/view?usp=sharing">Resume</a>
              </Box>
      </NAVWRAPPER>
  )
}

export default Navbar

const NAVWRAPPER = styled.div`
display:flex;
align-items:center;
gap:25px;
background-color:gray;

`;